
import Toast from 'vux/src/components/toast/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

export default {
  components: {
    Group,
    XInput,
    XButton,
    Toast
  },
  data () {
    return {
      codeTxt: '获取验证码',
      timer: 60,       //默认倒数30秒
      stop : false,   //默认是停止的，但界面加载之后会变成false
      Interval:null,  //setInterval的对象
      tips:"",
      showPositionValue:false,
      mobile:'',
      code:'',
      bindmobile:false
    }
  },
  mounted(){
    this.$http({
            method:'POST',
            url:this.$store.state.domain+'api/WechatAPI/IsBindCustomerPhone',
            body:{},
            headers: {"Authorization": this.$store.state.jwt.token}
            })
        .then((d) => {
                if(!d.data.success){
                  this.showPosition(d.data.message);
                  return false;
                }else{
                  if(d.data.content){
                    this.$router.push("/info")
                  }else{
                    this.bindmobile=true;
                  }
                }
              }, (d) => {
                console.log('网络繁忙！请稍候重再试');
        });
  },
  methods: {
    showPosition (t) {
      this.showPositionValue = true
      this.tips=t;
    },
    onSubmit(ev) {
       if(!this.mobile){
       this.showPosition("请输入手机号");
       return false;
       }
       this.$http({
                    method:'POST',
                    url:this.$store.state.domain+'api/WechatAPI/BindCustomerPhone',
                    body:{'phone':this.mobile,'code':this.code},
                    headers: {"Authorization": this.$store.state.jwt.token}
                    })
        .then((d) => {
                if(!d.data.success){
                  this.showPosition(d.data.message);
                  return false;
                }else{
                  this.$router.push("/info");
                }
              }, (d) => {
                this.showPosition('网络繁忙！请稍候重再试');
        });
      
    },
    update () {
        if(this.timer <= 0) 
        {
            this.timer = 60;
            this.codeTxt="获取验证码";
            clearInterval(this.Interval);
            this.stop = !this.stop;
        }
        else{
            this.timer--;
            this.codeTxt=this.timer+"S";
        }
    },
    startTimer () {
      if(!this.mobile){
        this.showPosition("请填写手机号");
        return false;
      }
        this.$http({
                    method:'POST',
                    url:this.$store.state.domain+'api/WechatAPI/SendSmsForBindCustomerPhone',
                    body:{'phone':this.mobile},
                    headers: {"Authorization": this.$store.state.jwt.token}
                    })
        .then((d) => {
                if(!d.data.success){
                  this.showPosition(d.data.message);
                  return false;
                }else{
                  //如果是false就开始倒计时，如果是true就停止倒计时
                    if(!this.stop)
                    {
                      this.stop = !this.stop;
                      this.Interval = setInterval(this.update,1000);    
                    }
                }
              }, (d) => {
                console.log('网络繁忙！请稍候重再试');
        });

        

        //this.stop = !this.stop;
    }
  }
}



